
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Satisfy", cursive,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.NavCircleButton {
  background-color: #cacaca;
  border-radius: 30px;
  border: 0;
  color: black;
  display: block;
  font-size: 12px;
  font-weight: bold;
  height: 60px;
  padding: 8px;
  text-align: center;
  text-decoration: none;
  width: 60px;
}

a.NavCircleButton {
  padding: 16px 8px 8px;
}

.NavCircleButton:hover {
  cursor: pointer;
}

.NoteListNav__list {
  list-style-type: none;
  margin: 121px 0 0;
  padding: 0;
}

.NoteListNav__button-wrapper {
  margin: 0;
}

.NoteListNav__add-folder-button {
  margin: 20px auto;
}
.NoteListNav__add-folder-button:hover {
  background-color: #29862e;
  color: white;
}

.NoteListNav__folder-link {
  background-color: #eecabd;
  color: #6f6e6e;
  display: block;
  font-family: var(--custom-font);
  font-size: 20px;
  margin: 0 0 10px auto;
  min-width: 200px;
  padding: 8px 16px;
  text-decoration: none;
}
.NoteListNav__folder-link:hover {
  color: #ffb72f;
  cursor: pointer;
}
.NoteListNav__folder-link.active {
  background-color: #102541;
}

.NoteListNav__num-notes {
  color: #adadad;
  display: block;
  float: right;
  font-size: 10px;
}

.Noteful-form {
  background-color: #1d3656;
  color: #dedede;
  padding: 16px;
  max-width: 332px;
}

.Noteful-form label {
  display: block;
  font-size: 16px;
  padding-bottom: 8px;
}

.Noteful-form input,
.Noteful-form textarea,
.Noteful-form select {
  font-size: 14px;
  padding: 8px;
  width: 300px;
}

.Noteful-form select {
  height: 33px;
}

.Noteful-form > .field {
  margin-bottom: 16px;
}
.Noteful-form > .buttons {
  margin-top: 26px;
}

.Noteful-form button {
  background-color: #010d1e;
  border-radius: 4px;
  border: 0;
  color: #cacaca;
  display: block;
  font-size: 18px;
  font-weight: bold;
  padding: 8px;
  text-align: center;
  text-decoration: none;
}
.Noteful-form button[type="submit"]:hover {
  background-color: #29862e;
  color: white;
}

.NotePageNav__back-button {
  margin: 45px 20px 25px auto;
}
.NotePageNav__back-button:hover {
  background-color: #a4b2b5;
}

.NotePageNav__folder-name {
  color: #5d9090;
  font-family: var(--custom-font);
  font-size: 40px;
  height: 50vh;
  margin: 0 0 0 auto;
  text-orientation: upright;
  width: 75px;
  -webkit-writing-mode: vertical-lr;
          writing-mode: vertical-lr;
}

.Noteful-form {
  background-color: white;
}

.Note {
  --note-height: 88px;
  background-color: #5d9090;
  color: white;
  height: var(--note-height);
  margin-bottom: 10px;
  max-width: 380px;
  padding: 8px 0 8px 8px;
  position: relative;
}

.Note__delete {
  --note-button-height: 32px;
  background-color: #bed0ca;
  border: 0;
  color: #6f6e6e;
  margin: 0;
  float: right;
  font-size: 12px;
  height: var(--note-button-height);
  position: relative;
  top: calc((var(--note-button-height) + 32px) * -1);
  -webkit-transform-origin: right bottom;
          transform-origin: right bottom;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  width: calc(var(--note-height));
}
.Note__delete:hover {
  background-color: #bd0909;
  color: white;
  cursor: pointer;
  font-weight: bold;
}

.Note__title {
  font-family: var(--custom-font);
  font-size: 20px;
  margin: 0;
  max-width: 80%;
  min-height: 24px;
}
.Note__title a {
  color: white;
  display: block;
  text-decoration: none;
}
.Note__title a:hover {
  color: #ffb72f;
}

.Note__dates {
  bottom: 8px;
  color: #c8c7c7;
  font-size: 14px;
  position: absolute;
}


.NoteListMain ul {
  list-style-type: none;
  margin: 0;
  padding: 10px;
}

.NoteListMain__button-container {
  max-width: 380px;
  margin: 0 10px;
}

.NoteListMain__add-note-button {
  margin: 0 auto 20px;
}
.NoteListMain__add-note-button:hover {
  background-color: #29862e;
  color: white;
}


.NotePageMain {
  background-color: #efefef;
  height: 100%;
  padding: 10px;
  width: 100%;
}

.NotePageMain__content {
  color: #3a3939;
  max-width: 380px;
}

.addFolder {
  color: whitesmoke;
  margin-left: 10px;
}

.newFolder {
  display: flex;
  flex-direction: column;
}

h2 {
  margin: 5px 0;
}

input {
  padding: 4px;
}

button {
  margin: 10px;
  padding: 5px;
}

.AddNote {
  color: whitesmoke;
  margin-left: 10px;
}

.newNote {
  display: flex;
  flex-direction: column;
}

input,
select {
  width: 70%;
  margin: 5px 0;
}
textarea {
  width: 80%;
  height: 200px;
  resize: none;
  margin: 5px 0;
}

button {
  width: 10%;
  margin: 5px 0;
  background-color: whitesmoke;
  padding: 5px;
  font-size: 1em;
}

.App {
  --custom-font: Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
    'Oxygen', 'Ubuntu', 'Cantarell', 'Satisfy', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif, cursive;
  display: grid;
  grid-template-areas: 'sidebar header' 'sidebar main';
  grid-template-columns: 1fr 3fr;
  grid-template-rows: 120px 1fr;
  min-height: 100vh;
}

.App__nav {
  background-color: white;
  grid-area: sidebar;
  height: 100vh;
  overflow: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.siteTitle {
  font-family: 'Crimson Text', serif;
  font-size: 2.7em;
  margin-top: 0;
}

.App__header {
  background-color: white;
  grid-area: header;
  padding-left: 16px;
  padding-top: 32px;
}

.App__header a {
  color: rgb(110, 151, 151);
  font-size: 1.2em;
  text-decoration: none;
}

.App__main {
  background-color: #a4b2b5;
  grid-area: main;
  height: calc(100vh - 120px);
  overflow: auto;
}
